  font-family: sans-serif;
  overflow:auto;
}

img {
  float: left;
  margin-right: 15px;
}



#splash {
  -webkit-animation: hideMe 8s 1;
          animation: hideMe 8s 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

#App {
  /* Photo by Nadine Primeau on Unsplash */
  background-image: url(https://i.ibb.co/XW03Rgs/nadine-primeau-b-Lk-T8w-GV0-I-unsplash.jpg);
  background-size: 100%;
  -webkit-animation: showMe 15s 1;
          animation: showMe 15s 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  /* animation-delay: 0.2s; */
}

#Apptitle {
  height: 120px;
  margin-left: 80px;
  margin-top: -50px;
}

#bars {
  height: 30px;
  left: 35%;
  margin: -10px 0 0 -20px;
  position: absolute;
  top: 50%;
  width: 40px;
  -webkit-animation: move 4s infinite;
          animation: move 4s infinite;
}

.bar {
  /* background: rgb(218, 210, 214); */
  background: green;
  bottom: 1px;
  height: 10px;
  position: absolute;
  width: 5px;
  -webkit-animation: sound 0ms -800ms linear infinite alternate;
          animation: sound 0ms -800ms linear infinite alternate;
}

@-webkit-keyframes sound {
  0% {
    opacity: .35;
    height: 3px;
  }

  100% {
    opacity: 1;
    height: 60px;
  }
}

@keyframes sound {
  0% {
    opacity: .35;
    height: 3px;
  }

  100% {
    opacity: 1;
    height: 60px;
  }
}

.bar:nth-child(1) {
  left: 1px;
  -webkit-animation-duration: 474ms;
          animation-duration: 474ms;
}

.bar:nth-child(2) {
  left: 5px;
  -webkit-animation-duration: 433ms;
          animation-duration: 433ms;
}

.bar:nth-child(3) {
  left: 9px;
  -webkit-animation-duration: 407ms;
          animation-duration: 407ms;
}

.bar:nth-child(4) {
  left: 13px;
  -webkit-animation-duration: 458ms;
          animation-duration: 458ms;
}

.bar:nth-child(5) {
  left: 17px;
  -webkit-animation-duration: 400ms;
          animation-duration: 400ms;
}

.bar:nth-child(6) {
  left: 21px;
  -webkit-animation-duration: 427ms;
          animation-duration: 427ms;
}

.bar:nth-child(7) {
  left: 25px;
  -webkit-animation-duration: 441ms;
          animation-duration: 441ms;
}

.bar:nth-child(8) {
  left: 29px;
  -webkit-animation-duration: 419ms;
          animation-duration: 419ms;
}

.bar:nth-child(9) {
  left: 33px;
  -webkit-animation-duration: 487ms;
          animation-duration: 487ms;
}

.bar:nth-child(10) {
  left: 36px;
  -webkit-animation-duration: 474ms;
          animation-duration: 474ms;
}

.bar:nth-child(11) {
  left: 45px;
  -webkit-animation-duration: 433ms;
          animation-duration: 433ms;
}

.bar:nth-child(12) {
  left: 49px;
  -webkit-animation-duration: 407ms;
          animation-duration: 407ms;
}

.bar:nth-child(13) {
  left: 55px;
  -webkit-animation-duration: 458ms;
          animation-duration: 458ms;
}

.bar:nth-child(14) {
  left: 67px;
  -webkit-animation-duration: 400ms;
          animation-duration: 400ms;
}

.bar:nth-child(15) {
  left: 71px;
  -webkit-animation-duration: 427ms;
          animation-duration: 427ms;
}



@-webkit-keyframes hideMe {
  0% {
    opacity: 1;
  }

  25% {
    opacity: 1;
  }

  75% {
    opacity: 0;
  }

  85% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}



@keyframes hideMe {
  0% {
    opacity: 1;
  }

  25% {
    opacity: 1;
  }

  75% {
    opacity: 0;
  }

  85% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}


@-webkit-keyframes showMe {
  0% {
    opacity: 0;
  }

  25% {
    opacity: 0;
  }

  50%{
    opacity:0;
  }

  65%{
    opacity:0.8;
  }

  75% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}


@keyframes showMe {
  0% {
    opacity: 0;
  }

  25% {
    opacity: 0;
  }

  50%{
    opacity:0;
  }

  65%{
    opacity:0.8;
  }

  75% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}

